module.exports = " <div class=jumbotron _v-3846b602=\"\"> <div class=container _v-3846b602=\"\"> <h1 _v-3846b602=\"\"><span class=\"glyphicon glyphicon-wrench\" _v-3846b602=\"\"></span>勞基法計算機</h1> <p _v-3846b602=\"\"> 沒人搞得清楚這次修法正確的計算方式，不如來個計算機自己按一按吧。 </p> </div> </div> <div class=container _v-3846b602=\"\"> <div class=handler _v-3846b602=\"\"> <a v-show=!expandDetail v-on:click=toggleExpanding _v-3846b602=\"\">[+] 展開詳細說明</a> <a v-show=expandDetail v-on:click=toggleExpanding _v-3846b602=\"\">[-] 收起詳細說明</a> </div> <div v-show=expandDetail class=\"alert alert-info\" _v-3846b602=\"\"> <span class=\"glyphicon glyphicon-pencil\" aria-hidden=true _v-3846b602=\"\"></span> <span _v-3846b602=\"\"> 前置條件： <ul _v-3846b602=\"\"> <li _v-3846b602=\"\"><a target=_blank href=\"http://laws.mol.gov.tw/Chi/FLAW/FLAWDOC03.asp?keyword=&amp;lc1=FL014930%2C+20150701%2C+24&amp;sdate=&amp;edate=&amp;datatype=etype&amp;recordNo=7\" _v-3846b602=\"\">勞動 2 字第 0960130677 號 函</a>：原約定月薪給付總額相當於 240 小時者（即「平日每小時工資額」係以月薪總額除以 30 再除以 8 核計者）</li> <li _v-3846b602=\"\">現行勞基法與一例一休的例假日假設為週日，兩例的例假日為週六與週日</li> <li _v-3846b602=\"\">現行勞基法的週六假設為約定不用上班的日子</li> <li _v-3846b602=\"\">一例一休的休息日假設為星期六</li> <li _v-3846b602=\"\">基於以上假設，在計算輪班制度時（例如四班二輪）可能會與實際狀況有誤差</li> <li _v-3846b602=\"\">下面的「額外工資」欄位包含加班費與例假日上班的工資加倍發給</li> <li _v-3846b602=\"\">原有七天假期由於以前的雙週八十四小時工時，所以週休二日的勞工沒有休假，但應映現行法令改為單週工時四十小時，週休二日的勞工也應該要放這七天，詳情請見 <a target=_blank href=\"https://youtu.be/4gd2m_73NHE?t=23m13s\" _v-3846b602=\"\">這裡</a>。</li> </ul> </span> </div> <h2 _v-3846b602=\"\">條件設定</h2> <p _v-3846b602=\"\">假設勞工採月薪制，其月薪 <input class=monthly-pay number=\"\" type=number v-model=monthlyPay _v-3846b602=\"\"> 元，每月總工時為 {{assumingWorkHours}} 計算，平均時薪為 {{hourlyPay.toFixed(2)}} 元。</p> <p class=dayoff-options _v-3846b602=\"\"> 例假日工作條件設定：<br _v-3846b602=\"\"> <label _v-3846b602=\"\"><input type=radio value=disaster v-model=regularDayOffWorkReason _v-3846b602=\"\"> 發生天災、事變或突發事件，雇主停止休假要求勞工出勤</label><br _v-3846b602=\"\"> <label _v-3846b602=\"\"><input type=radio valu